	module.exports = {"data":{"post":{"html":"<p>Brent Ashley sings <a href=\"http://brentashley.blogchat.com/archives/000369.html#000369\">The Praises of SSH</a> (in particular, <a href=\"http://www.openssh.org/\">OpenSSH</a>), and <a href=\"http://traumwind.tierpfad.de/blog/?detail=2002-06-03_23-07\">Martin</a> and <a href=\"http://inessential.com/?comments=1&amp;postid=2019\">Brent Simmons</a> say \"Amen\". I can only add that running <a href=\"http://www.rsync.org/\">rsync</a> (like scp, but faster and better) and <a href=\"http://www.cvshome.org/\">cvs</a> (version control) over ssh make it cooler yet, and that <a href=\"http://tramp.sourceforge.net/\">tramp for (x)emacs</a> and the <a href=\"http://www.vim.org/html/pi_netrw.html\">netrw</a> functions in <a href=\"http://www.vim.org/\">vim</a> enable you to locally edit files on your ssh server.</p>","fields":{"title":"Sing the Praises of SSH","date":"04 Jun 2002","url":"https://papascott.de/2002-06-04-sing-the-praises-of-ssh/"}}},"pathContext":{"slug":"/2002-06-04-sing-the-praises-of-ssh/","prev":{"fields":{"title":"Christopher Update, Without Pictures","date":"04 Jun 2002","slug":"/2002-06-04-christopher-update-without-pictures/"}},"next":{"fields":{"title":"Mozilla 1.0","date":"05 Jun 2002","slug":"/2002-06-05-mozilla-10/"}}}}

